.progress-bar {
  border-radius: 0px;
  overflow: hidden;
  width: 100%;
}
.progress-bar span {
  display: block;
}

.bar {
  background: rgba(0, 0, 0, 0);
  color: white;
}

.progress-wordpress {
  -webkit-animation: loader-wordpress 3s ease;
  animation: loader-wordpress 3s ease;
  background-color: #c6bce9;
  color: #fff;
  padding: 10px;
  margin-bottom: 5px;
  width: 70%;
}

.progress-woocommerce {
  -webkit-animation: loader-woocommerce 3s ease;
