.record {
  width: 500px;
  height: 250px;
  overflow: hidden;
	border-radius: 5px;
  background: linear-gradient(87deg, #251170b3 0%, rgba(0, 0, 0, 0.7) 100%);
  transition: box-shadow 300ms;

}
.record:hover {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
	border-radius: 0px;
}

.record-display {
  width: 100%;
  height: 100%;
  top: 0;
  background-size: cover !important;
  background-position: center center !important;
	background-repeat: no-repeat !important;
}

.record:hover .record-display {
  height: 0%;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 2px rgba(0, 0, 0, 0.24);
}

.record-desc {
  margin: 20px 0 0 10px;
  opacity: 0;
  transition: opacity 400ms ease-in-out 190ms;
}

#record1 {
  transition: height 700ms cubic-bezier(0.54, 0.21, 0.18, 1.35);
}

.record:hover .record-desc {
  opacity: 1;
}

